.navbar {
  display: flex;
  justify-content: center; 
  background-color: #cca8e9; 
  padding: 15px 10px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  width: 100%;
  align-items: center; 
}

.nav-link {
  color: white; 
  text-decoration: none;
  padding: 0px 200px; 
  border-radius: 5px;
  margin: 0; 
  transition: background-color 0.3s;
  font-size: 2rem;
}

.nav-link:hover {
  background-color: #cadefc;
}



.content {
  width: 100%;
  max-width: 800px; 
  padding: 50px;
  border-radius: 8px;
}

.section {
  padding: 20px;
  margin: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #c3bef0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  height: 300px;
  width: 800px;
}

h1 {
  font-size: 2em;
  color: #333;
  margin-left: 300px;
}

p {
  font-size: 1.2em;
  color: #666;
  margin-left: 130px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}